import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import TwitterIcon from '@material-ui/icons/Twitter';
import Typography from '@material-ui/core/Typography';
import logo from "../images/foreverDAO_42_white_all2.png";
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';



const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  paper: {
    marginRight: theme.spacing(2),
  },
  appbar:{
    backgroundColor:"#ffd22a",
    opacity: 1.00,
    borderTop: 0,
    borderLeft: 0,
    borderRight:0,
    paddingBottom:  theme.spacing(0),

    border: "1px solid rgba(0, 0, 0, 0.12)",

    boxShadow: '0 15px 15px -3px rgba(0,0,0,.1)',
  },
  appbarContainer:{
    paddingLeft: 0,
    paddingRight: 0,

  },
  divFlex:{
    flexGrow: 1,
  },
  link: {
    margin: theme.spacing(1, 1.5),

    fontWeight: '500',

  },
  logo:{
    padding: theme.spacing(2, 0,1),
    justifyContent: "center",
  },
  menu:{
    border: '1px solid #000000',
    color: '#000000',
  },
  menuItem:{
    color: '#000000',
  },
  paperMenu:{
    border: '1px solid #000000',
    padding:  theme.spacing(1),
  }
}));

const Navbar = () => {
  const classes = useStyles();



  const preventDefault = (event) => event.preventDefault();
  return(
    <AppBar elevation={0} variant="" position="fixed" className={classes.appbar}>
      <Container maxWidth="md" disableGutters>
      <Toolbar  display="flex" flexDirection="row" disableGutters>
        <Link href="https://foreverdao.org" className={classes.logo}>
        <img src={logo} alt="Forever Logo" />
        </Link>
        <Typography variant="h6" component="div" className={classes.divFlex}>

        <Link variant="subtitle1" color="textPrimary" href="https://shapes.foreverdao.org" className={classes.link} >

        </Link>

        </Typography>


        <Link href="https://twitter.com/foreverdao_" >
         <IconButton
            size="large"
            aria-label="show more"
            color="primary"
          >
            <TwitterIcon />
          </IconButton>
          </Link>
      </Toolbar>
      </Container>

    </AppBar>
  );
}

export default Navbar;
