import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Chip from '@material-ui/core/Chip';
import ReactPlayer from 'react-player'

import vidShape from '../videos/3094.mp4';
import shape726 from '../videos/726.mp4';
import shape1261 from '../videos/1261.mp4';
import shape1743 from '../videos/1743.mp4';
import shape3735 from '../videos/3735.mp4';
import shape4470 from '../videos/4470.mp4';
import shape4435 from '../videos/4435.mp4';

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {

    padding: theme.spacing(4, 0, 6),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: '#fafafa',
    paddingBottom:  theme.spacing(0),
    minWidth: 275,
    border: "1px solid rgba(0, 0, 0, 0.12)",

    boxShadow: '0 15px 15px -3px rgba(0,0,0,.1)',
  },
  cardTitle:{
    paddingBottom:  theme.spacing(1),
    fontFamily: [
      'Poppins',
      'sans-serif',
    ],
  },
  cardTitleBlack:{
    paddingBottom:  theme.spacing(1),
    fontFamily: [
      'Poppins',
      'sans-serif',
    ],
    color:'#ffffff',
  },
  sectionHeading:{

    fontSize: '2.5rem',
    fontFamily: [
      'Poppins',
      'sans-serif',
    ],
  },
  cardBlack: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: '#000000',
    paddingBottom:  theme.spacing(0),
    minWidth: 275,
    border: "1px solid rgba(0, 0, 0, 0.12)",

    boxShadow: '0 15px 15px -3px rgba(0,0,0,.22)',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
    paddingBottom:  theme.spacing(2),
  },
  whiteFont:{
    color:'#ffffff',
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  chipWhite:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#ffffff',
    color:'#000000',
  },
  chipRed:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#EA3323',
    color:'#000000',
  },
  chipYellow:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#FFBB25',
    color:'#000000',
  },
}));

class ResponsivePlayer extends React.Component {
  render () {
    return (
      <div className='player-wrapper'>
        <ReactPlayer
          className='react-player'
          url={vidShape}
          width='100%'
          height='100%'
          playing={true}
          loop='true'
          muted={true}
        />
      </div>
    )
  }
}

export default function Album() {
  const classes = useStyles();

  return (
    <Container maxWidth="xl" component="main" className={classes.heroContent}>
      <Container maxWidth="md" component="main" className={classes.heroContent}>
              <Typography gutterBottom  className={classes.sectionHeading}>
                ForeverShapes
              </Typography>
              <Typography>
              5000 seamlessly looping, 10 second, 60 FPS generative art.<br/> <strong>Utility:</strong> community experiences and governance for the charity.
              </Typography>
      </Container>
          <Grid container spacing={4}>
          <Grid item  xs={12} sm={6} md={4} lg={3}>
            <Card elevation="5" className={classes.card}>

              <ResponsivePlayer />
              <CardContent className={classes.cardContent}>
                <Typography  className={classes.cardTitle} variant="h6" align="center" color="textPrimary" component="p">
                  SHAPE #3094
                </Typography>
                <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
                  Wireframe • Cuboid • Lines • Mini
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item  xs={12} sm={6} md={4} lg={3}>
            <Card variant="outlined" className={classes.card}>
              <ReactPlayer
                className='react-player'
                url={shape726}
                width='100%'
                height='100%'
                playing={true}
                loop='true'
                muted={true}
              />
              <CardContent className={classes.cardContent}>
                <Typography  className={classes.cardTitle} variant="h6" align="center" color="textPrimary" component="p">
                  SHAPE #726
                </Typography>
                <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
                  Wireframe • Cuboid • Random • Regular
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item  xs={12} sm={6} md={4} lg={3}>
            <Card variant="outlined" className={classes.card}>
              <ReactPlayer
                className='react-player'
                url={shape1743}
                width='100%'
                height='100%'
                playing={true}
                loop='true'
                muted={true}
              />
              <CardContent className={classes.cardContent}>
                <Typography  className={classes.cardTitle} variant="h6" align="center" color="textPrimary" component="p">
                  SHAPE #1743
                </Typography>
                <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
                  Classic • Cone • Random • Mega
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item  xs={12} sm={6} md={4} lg={3}>
            <Card variant="outlined" className={classes.cardBlack}>
              <ReactPlayer
                className='react-player'
                url={shape4470}
                width='100%'
                height='100%'
                playing={true}
                loop='true'
                muted={true}
              />
              <CardContent className={classes.cardContent}>
                <Typography  className={classes.cardTitleBlack} variant="h6" align="center" color="textPrimary" component="p">
                  SHAPE #4470
                </Typography>
                <Typography variant="subtitle1" align="center" color="textSecondary" component="p" className={classes.whiteFont}>
                  Classic • Cuboid • Random • Mega
                </Typography>
              </CardContent>

            </Card>
          </Grid>
          </Grid>



          
</Container>

  );
}
