import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import TwitterIcon from '@material-ui/icons/Twitter';
import Typography from '@material-ui/core/Typography';
import logo from "../images/logo_42b.png";
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      Forever Together (42✨)

    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  '@global': {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: 'none',
    },
  },
  appbar:{
    backgroundColor:"#fed229",
    opacity: 1.00,
    borderTop: 0,
    borderLeft: 0,
    borderRight:0,
  },
  divFlex:{
    flexGrow: 1,
  },
  link: {
    margin: theme.spacing(1, 1.5),
    flexGrow: 1,
  },
  logo:{
    padding: theme.spacing(2, 0,1),
    justifyContent: "center",
  },
  footer: {
    borderTop: `1px solid ${theme.palette.divider}`,
    marginTop: theme.spacing(8),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up('sm')]: {
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(6),
    },
    fontWeight: 200,
  },
}));
const footers = [
  {
    title: 'ForeverDAO',
    description: ['Treasury', 'Discord', 'Telegram', ''],
    link: ['https://foreverdao.org','https://foreverdao.org','https://foreverdao.org'],
  },
  {
    title: 'Projects',
    description: ['Charity', 'Shapes', 'Coins'],
    link: ['https://foreverdao.org','https://foreverdao.org','https://foreverdao.org'],
  },
  {
    title: 'Resources',
    description: ['Solana', 'Metaplex', 'P5JS'],
    link: ['https://foreverdao.org/test','https://foreverdao.org','https://foreverdao.org'],
  },

];

const Footer = () => {
  const classes = useStyles();
  const preventDefault = (event) => event.preventDefault();
  return(

      <Container maxWidth="md" component="footer" className={classes.footer}>
        <Grid container spacing={4} justifyContent="space-evenly">

            <Grid item xs={4} sm={4}>
              <Typography variant="h6" color="textPrimary" gutterBottom>
                ForeverDAO
              </Typography>
              <ul>
                  <li>
                    <Link href="#" variant="subtitle1" color="textSecondary">
                      Treasury
                    </Link>
                  </li>
                  <li>
                    <Link href="#" variant="subtitle1" color="textSecondary">
                      Discord
                    </Link>
                  </li>
                  <li>
                    <Link href="https://twitter.com/ForeverDAO_" variant="subtitle1" color="textSecondary">
                      Twitter
                    </Link>
                  </li>
              </ul>
            </Grid>
            <Grid item xs={4} sm={4}>
              <Typography variant="h6" color="textPrimary" gutterBottom>
                Projects
              </Typography>
              <ul>
                  <li>
                    <Link href="https://foreverdao.org" variant="subtitle1" color="textSecondary">
                      ForeverCharity
                    </Link>
                  </li>
                  <li>
                    <Link href="https://shapes.foreverdao.org" variant="subtitle1" color="textSecondary">
                      ForeverShapes
                    </Link>
                  </li>
                  <li>
                    <Link href="https://foreverdao.org" variant="subtitle1" color="textSecondary">
                      ForeverCoins
                    </Link>
                  </li>
              </ul>
            </Grid>
            <Grid item xs={4} sm={4}>
              <Typography variant="h6" color="textPrimary" gutterBottom>
                Resources
              </Typography>
              <ul>
                  <li>
                    <Link href="https://solana.com" variant="subtitle1" color="textSecondary">
                      Solana
                    </Link>
                  </li>
                  <li>
                    <Link href="https://www.metaplex.com" variant="subtitle1" color="textSecondary">
                      Metaplex
                    </Link>
                  </li>
                  <li>
                    <Link href="https://p5js.org" variant="subtitle1" color="textSecondary">
                      P5JS
                    </Link>
                  </li>
              </ul>
            </Grid>
        </Grid>
        <Box mt={5}>
          <Copyright />
        </Box>
      </Container>

  );
}

export default Footer;
