import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import TwitterIcon from '@material-ui/icons/Twitter';
import Typography from '@material-ui/core/Typography';
import logo from "../images/Forever_bg_600b.png";
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import FontBold from '../fonts/montserrat-alternates.ttf';
import StarIcon from '@material-ui/icons/StarBorder';
import Button from '@material-ui/core/Button';
import AddressBalance from './AddressBalance';

const FontAlt = {
  fontFamily: 'montserrat-alternates',
  fontStyle: 'bold',
  fontDisplay: 'swap',
  fontWeight: 900,
  src: `
    local('montserrat-alternates'),
    url(${FontBold}) format('ttf')
  `,
  unicodeRange:
    'U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF',
};

const useStyles = makeStyles((theme) => ({
  '@global': {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: 'none',
    },
  },
  sectionHeading:{

    fontSize: '2.5rem',
    fontFamily: [
      'Poppins',
      'sans-serif',
    ],
  },
  heroContent: {
    padding: theme.spacing(8, 0, 2),

  },
  heroText: {
    fontWeight: 900,

     fontFamily: 'montserrat-alternates',
  },
  divFlex:{
    flexGrow: 1,
  },
  link: {
    margin: theme.spacing(1, 1.5),
    flexGrow: 1,
  },
  logo:{
    padding: theme.spacing(2, 0,1),
    justifyContent: "center",
  },
  footer: {
    borderTop: `1px solid ${theme.palette.divider}`,
    marginTop: theme.spacing(8),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up('sm')]: {
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(6),
    },
    fontWeight: 200,
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: '#fafafa',
    padding:  theme.spacing(3),

  },
}));



const Hero = () => {
  const classes = useStyles();
  const preventDefault = (event) => event.preventDefault();
  return(

    <Container maxWidth="sm" component="main" className={classes.heroContent}>
      <Container maxWidth="md" component="main" className={classes.heroContent}>
        <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom className={classes.heroText}>

        </Typography>
        <Grid container spacing={1}>

          <Grid item xs={6} sm={6}>
            <Typography variant="h4" align="center" color="textPrimary" component="p" className={classes.sectionHeading}>
              <AddressBalance />
            </Typography>
            <Typography variant="subtitle1" align="center" color="textPrimary" component="p">
              Charity Wallet
            </Typography>
          </Grid>


          <Grid item xs={6} sm={6}>
            <Typography variant="h4" align="center" color="textPrimary" component="p" className={classes.sectionHeading}>
              $0
            </Typography>
            <Typography variant="subtitle1" align="center" color="textPrimary" component="p">
              Total Donations
            </Typography>
          </Grid>
        </Grid>


      </Container>




    </Container>

  );
}

export default Hero;
