export default function Leaderboard() {
  const classes = useStyles();

  return (
    <Container maxWidth="md" component="main" className={classes.heroContent}>

    <Container maxWidth="md" component="main" className={classes.heroContent}>
            <Typography gutterBottom  className={classes.sectionHeading}>
              Leaderboards
            </Typography>
            <Typography>
              Leaderboards of top donations and royalities to charity wallet.<br/> <strong>Monthly prizes:</strong> ForeverCoins for first place. (in development)
            </Typography>
    </Container>
          <Grid container spacing={4}>
          <Grid item  xs={12} sm={6} md={6}>




              <CardContent className={classes.cardContent}>
                <Typography  className={classes.cardTitle} variant="h6" align="center" color="textSecondary" component="p">
                  Donation Wallets
                </Typography>


                <TableContainer>
                  <Table className={classes.table} aria-label="simple table">

                    <TableBody>
                      {rowsWallets.map((row) => (
                        <TableRow key={row.name}>
                          <TableCell component="th" scope="row">
                            {row.name}
                          </TableCell>
                          <TableCell align="right">{row.calories}</TableCell>
                          <TableCell align="right">{row.fat}</TableCell>
                          <TableCell align="right">{row.carbs}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>


              </CardContent>


          </Grid>

          <Grid item  xs={12} sm={6} md={6}>


              <CardContent className={classes.cardContent}>
                <Typography  className={classes.cardTitle} variant="h6" align="center" color="textSecondary" component="p">
                  NFT Royalties
                </Typography>


                <TableContainer>
                  <Table className={classes.table} aria-label="simple table">

                    <TableBody>
                      {rowsNFT.map((row) => (
                        <TableRow key={row.name}>
                          <TableCell component="th" scope="row">
                            {row.name}
                          </TableCell>
                          <TableCell align="right">{row.calories}</TableCell>
                          <TableCell align="right">{row.fat}</TableCell>
                          <TableCell align="right">{row.carbs}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>

              </CardContent>


          </Grid>

          </Grid>

</Container>

  );
}
