import React from 'react';
import axios from 'axios';

const baseURL = "https://public-api.solscan.io/account/";
var address = "HDiWmKajwPgMwfq5qpqHL6BKK4uqdMSSXRUEXCeVTsvr"; // GR Mint
//const address = "r7wFodXbCtqisbo9xdfyykzQ98V5mTdCbvTSmY7RChp"; // GR Shapes royalities

export default function AddressBalance() {
  const [res, setRes] = React.useState(null);

  React.useEffect(() => {
    axios.get(baseURL+address).then((response) => {
      setRes(response.data);
    });
  }, []);

  if (!res) return "--";

  return (
    <div>

      {res.lamports/1000000000}
    </div>
  );
}

